.modal {
	&--program-rules {
		.modal__content {
			width: 100%;
		}
		.program-rules {
			&__title {
				font-style: normal;
				font-weight: 600;
				font-size: 16px;
				line-height: 20px;
				margin: 0;
				padding: 0;
				margin: 0 0 20px 0;
			}
			&__list {
				padding: 0 0 0 15px;
				&-item {
					margin: 0 0 30px 0;
					font-weight: normal;
					font-size: 14px;
					line-height: 20px;
				}
			}
			&__sub-list {
				padding: 0 0 0 20px;
				margin: 10px 0 0 0;
				&-item {
					&::marker {
						font-size: 7px;
					}
				}
			}
		}
	}
}
