.modal-success-message__icon-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 40px;
  height: 40px;
  margin: 0 auto 20px;
}

.modal-success-message__icon {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 35px;
  height: 35px;

  background-color: $color-fruit-salad;
  border-radius: 50%;

  svg {
    width: 16px;
    height: 12px;
  }
}

.modal-success-message__message {
  margin: 0;

  font-size: 14px;
  line-height: 1.43;
  text-align: center;
}
