@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

@mixin vp--1280 {
  @media (max-width: $vp--1280) {
    @content;
  }
}

@mixin vp--1239 {
  @media (max-width: $vp--1240 - 1) {
    @content;
  }
}

@mixin vp--1023 {
  @media (max-width: $vp--1024 - 1) {
    @content;
  }
}

@mixin vp--768 {
  @media (max-width: $vp--768) {
    @content;
  }
}

@mixin vp--640 {
  @media (max-width: $vp--640) {
    @content;
  }
}

@mixin vp--425 {
  @media (max-width: $vp--425) {
    @content;
  }
}

@mixin reset-btn {
  padding: 0;
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  background-color: $color-transparent;
  border: none;
  outline: none;

  &:not(:disabled) {
    cursor: pointer;
  }
}

@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
