.contact-us {
  padding: 80px 0;

  background-color: $color-fantasy;
}

.contact-us__container {
  display: grid;
  grid-template-columns: 360px 1fr;
  gap: 40px;

  @include vp--1280 {
    padding: 0 30px;
  }
  @include vp--1023 {
    display: block;
  }
  @include vp--768 {
    padding: 0 70px;
  }
  @include vp--640 {
    padding: 0 35px;
  }
  @include vp--425 {
    padding: 0 20px;
  }
}

.contact-us__contacts {
  margin-bottom: 80px;
}

.contact-us__title {
  margin: 0;
  padding-bottom: 30px;

  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 1.21;
  text-transform: uppercase;
}

.contact-us__subtitle {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 18px;
  line-height: 1.39;
}

.contact-us__links {
  @include reset-list;
}

.link.contact-us__link {
  font-size: 14px;
  line-height: 1.71;
}

.contact-us__input {
  width: 100%;
  padding: 0;
  margin-bottom: 14px;

  label {
    input {
      padding: 5px 0;

      background-color: $color-transparent;
      border: none;
      border-bottom: 1px solid $color-semi-sweet-chocolate;
      border-radius: 0;
    }
  }

  &.is-invalid {
    label {
      input {
        &::placeholder {
          color: $color-alizarin;
        }
      }
    }
  }

  &.is-valid {
    label {
      input {
        color: $color-fruit-salad;
      }
    }
  }
}

.contact-us__checkbox {
  padding: 0;
  margin: 20px 0;
}

.contact-us__notification {
  overflow: hidden;
  max-height: 0;

  color: $color-alizarin;

  transition: max-height $trans--300;

  span {
    display: block;
    padding-bottom: 20px;
  }
}

.contact-us__submit-btn {
  margin-top: 20px;
}

._for-nursery {
  .contact-us {
    &__link {
      display: block;
      margin: 5px 0 30px 0;
    }
  }
}

