.custom-input {
  position: relative;

  display: flex;
  width: 500px;
  padding: 20px 0;

  label {
    display: flex;
    align-items: center;
    width: 100%;

    input {
      flex: 1 1 auto;
      padding: 9px 19px;

      font-size: 14px;
      line-height: 1.43;

      color: $color-semi-sweet-chocolate;

      border: 1px solid rgba($color-semi-sweet-chocolate, 0.3);
      border-radius: 5px;
      outline: none;

      transition: border-color $trans--300;

      &:hover,
      &:focus {
        border-color: rgba($color-olive-drab, 0.3);
      }

      &::placeholder {
        color: rgba($color-semi-sweet-chocolate, 0.6);
      }
    }

    @include vp--1023 {
      flex-direction: column;
      align-items: flex-start;

      input {
        width: 100%;
        margin-top: 5px;
      }
    }
  }

  .custom-input__label {
    flex: 0 0 180px;

    margin-right: 20px;

    @include vp--1023 {
      flex-basis: auto;
    }
  }

  &.is-invalid {
    label {
      color: $color-alizarin;
    }

    .custom-input__error {
      opacity: 1;
    }
  }

  &.is-valid {
    label {
      color: $color-fruit-salad;
    }

  }
}

.custom-input__error {
  position: absolute;
  right: 0;
  bottom: 0;

  color: orange;

  opacity: 0;

  transition: opacity $trans--300;
}
