.account {
  padding: 200px 0 80px;

  @include vp--1239 {
    padding-top: 120px;
  }

  @include vp--768 {
    padding-bottom: 40px;
  }
}

.account__title {
  position: relative;

  margin: 0;
  padding-bottom: 21px;

  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 1.21;
  text-transform: uppercase;

  &::before {
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    right: 0;

    width: 100%;
    height: 1px;

    background-image: $green-to-yellow-right;
  }
  @include vp--425 {
    font-size: 14px;
  }
}

.account__wrapper {
  display: grid;
  grid-template-columns: 360px 1fr;
  grid-gap: 40px;

  @include vp--1023 {
    grid-template-columns: 295px 1fr;
  }

  @include vp--768 {
    display: block;
  }
}

.account__menu {
  position: relative;

  padding: 40px 20px;

  background-color: rgba($color-olive-drab, 0.05);

  @include vp--768 {
    padding: 0;
  }
}

.account__menu-btn {
  position: relative;
  z-index: 3;
  display: none;

  width: 100%;
  padding: 20px;

  font-weight: 600;
  font-size: 14px;
  line-height: 1.43;
  text-transform: none;

  color: $color-olive-drab;

  background-color: #f8f9f3;
  border: none;
  box-shadow: none;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:not(:disabled):hover,
    &:focus {
      color: $color-semi-sweet-chocolate;
      background-color: #f8f9f3;
    }
  }

  @include vp--768 {
    display: flex;
    justify-content: space-between;
    text-align: left;
  }
}

.account__menu-btn-icon {
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  margin-left: auto;

  background-color: $color-semi-sweet-chocolate;

  transition: transform $trans--600;

  clip-path: polygon(25% 40%, 75% 40%, 50% 65%);

  .account__menu.open & {
    transform: rotate(180deg);
  }
}

.account__menu-content {
  @include vp--768 {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
    max-height: 60px;
    overflow: hidden;
    visibility: hidden;
    opacity: 0;
    box-shadow: none;
    transition: max-height $trans--600, box-shadow $trans--600, opacity $delay--600, visibility $delay--600;

    .account__menu.open & {
      visibility: visible;
      opacity: 1;

      box-shadow: $shaddow-list;

      transition: max-height $trans--600, box-shadow $trans--600, opacity 0s, visibility 0s;
    }
  }
}

.account__menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background-color: $color-transparent;
  cursor: pointer;
  transition: background-color $trans--600;

  .account__menu.open & {
    background-color: rgba($color-semi-sweet-chocolate, 0.5);
  }
}

.account__menu-list {
  @include reset-list;

  &:not(:last-child) {
    margin-bottom: 40px;
    padding-bottom: 40px;
    border-bottom: 1px solid rgba($color-semi-sweet-chocolate, 0.3);
  }
}

.account__menu-list-item {
  &:not(:last-child) {
    margin-bottom: 14px;
  }
  &.current > .account__menu-link {
    font-weight: bold;
  }

  @include vp--768 {
    // &.current {
    //   margin-bottom: 0;
    // }
  }
}

.account__menu-link {
  font-size: 14px;
  line-height: 1.43;

  color: $color-semi-sweet-chocolate;

  &:not([href]) {
    // font-weight: 600;

    color: $color-olive-drab;
  }

  @include vp--768 {
    &:not([href]) {
      display: none;
    }
  }
}

.account__instructions {
  &:not(:last-child) {
    margin-bottom: 40px;
    padding-bottom: 40px;
    border-bottom: 1px solid rgba($color-semi-sweet-chocolate, 0.3);
  }

  p {
    margin: 0;
    font-style: italic;
    font-size: 12px;
    line-height: 1.5;
    color: rgba($color-semi-sweet-chocolate, 0.8);

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

.account__instructions._hidden {
  display: none;
}

.account__content {
  padding-top: 40px;
  &-articles {
    &-block {
      width: 100%;
      &-items {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
        grid-gap: 20px;
        @include vp--425 {
          grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
        }
      }
    }
    &-item {
      &-title {
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        margin: 0.9em 0;
        text-align: left;
      }
      &-link {
        display: block;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        text-decoration: underline;
        &:hover {
          text-decoration: none;
        }
      }
    }
  }
  &-article {
    &-title {
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 24px;
      margin: 0 0 1em 0;
    }
    &-pic {
      margin: 20px 0 10px 0;
      width: 100%;
    }
    &-img {
      width: 100%;
      height: 500px;
      max-width: 100%;
      object-fit: cover;
    }
    &-text {
      margin: 1em 0;
    }
  }
}

.account__content-title-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 20px;

  @include vp--1023 {
    display: block;
  }
}

.account__content-title {
  margin: 0;

  font-weight: 600;
  font-size: 18px;
  line-height: 1.33;

  &--limit-width {
    width: 200px;
  }
  @include vp--768 {
    font-size: 14px;
  }
}

.account__notification {
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;

  transition: max-height $trans--300;

  &--success {
    color: $color-fruit-salad;
  }

  &--error {
    color: $color-alizarin;
  }

  @include vp--1023 {
    overflow: hidden;
    max-height: 0;

    span {
      display: block;
      padding-top: 20px;
    }
  }
}

.account-form__input {
  width: 100%;
  padding: 0;

  &:not(:last-child) {
    margin-bottom: 10px;
  }
}

.account-form__select {
  width: 100%;
  padding-top: 0;
  padding-bottom: 0;

  &:not(:last-child) {
    margin-bottom: 10px;
  }

  .custom-select__list {
    width: calc(100% - 200px);
  }

  @include vp--1023 {
    .custom-select__list {
      width: 100%;
    }
  }
}

.account-form__file-upload {
  display: flex;
  align-items: center;
  margin: 20px 0 40px;

  @include vp--1023 {
    display: block;

    margin-top: 10px;
  }
}

.account-form__file-desc {
  flex: 0 0 180px;
  align-items: center;

  margin-right: 20px;

  @include vp--1023 {
    margin-right: 0;
    margin-bottom: 10px;
  }
}

.account-form__file-input {
  input {
    display: none;
  }
}

.account-form__file-label {
  display: flex;
  align-items: center;

  font-size: 14px;
  line-height: 1.43;
  text-decoration-line: underline;
  text-decoration-skip-ink: none;

  svg {
    width: 20px;
    height: 20px;
  }

  span:not(:first-child) {
    margin-left: 10px;
  }
}

.account-form__checkbox {
  width: 100%;
  padding: 0;

  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.account {
  &__container {
    @include vp--1280 {
      padding: 0 30px;
    }
    @include vp--768 {
      padding: 0 70px;
    }
    @include vp--640 {
      padding: 0 35px;
    }

    @include vp--425 {
      padding: 0 20px;
    }
  }
  &__benefits-more-list-item {
    list-style: disc;
  }
  &__dates-list-item {
    list-style: disc;
  }
}
._for-manufacturers {
  .account {
    &__title {
      &::before {
        background-image: $blue-to-light-blue-right;
      }
    }
    &__menu {
      background-color: rgba($color-frosty-sky, 0.05);
      &-inner {
        background-color: inherit;
        @include vp--768 {
          background-color: $color-white;
          padding: 70px 20px 20px 20px;
        }
      }
      &-list:not(:last-child) {
        border-bottom: 1px solid rgba($color-blue-for-MF, 0.3);
      }
      &-list.border-none {
        border: none;
      }
      &-list {
        &-item._is-authorized > .account__menu-link {
          color: $color-semi-sweet-chocolate;
        }
        &-item.current > .account__menu-link {
          color: $color-blue-for-MF;
        }
      }
      &-link {
        color: rgba($color-semi-sweet-chocolate, 0.4);
        &:not([href]) {
          color: $color-blue-for-MF;
