            font-weight: normal;
            color: $color-blue-for-MF;
            text-align: left;
            border: 1px solid rgba($color-semi-sweet-chocolate, 0.3);
            &:first-of-type {
              flex: 0 0 189px;
            }
            &:last-of-type {
              flex: 0 0 90px;
              padding: 10px 0 10px 10px;
            }
          }
        }
        tbody {
          width: 100%;
          tr {
            width: 100%;
            display: flex;
            td {
              flex: 0 0 100px;
              display: flex;
              justify-content: flex-start;
              align-items: center;
              padding: 10px;
              font-size: 12px;
              line-height: 16px;
              font-style: normal;
              font-weight: normal;
              color: $color-semi-sweet-chocolate;
              text-align: left;
              border: 1px solid rgba($color-semi-sweet-chocolate, 0.3);
              &:first-of-type {
                flex: 0 0 189px;
              }
              &:last-of-type {
                flex: 0 0 90px;
              }
            }
          }
        }
        &-title {
          width: inherit;
          font-style: normal;
          font-weight: normal;
          font-size: 16px;
          line-height: 20px;
          background-color: $color-blue-for-MF;
          color: $color-white;
          padding: 10px;
          height: 40px;
          span {
            position: absolute;
          }
        }
        &-title.table-finished-products {
          width: 805px;
        }
      }
      &-table.table-finished-products {
        width: 805px;
        th {
          &:nth-child(4) {
            flex: 0 0 125px;
          }
          &:last-of-type {
            flex: 0 0 90px;
          }
        }
        td {
          &:nth-child(4) {
            flex: 0 0 125px;
          }
          &:last-of-type {
            flex: 0 0 90px;
          }
        }
      }
      &-table.table-calculations {
        margin-top: 24px;
      }
      &-btn-block {
        width: 100%;
        margin-top: 20px;
        &-form {
          width: 100%;
          max-width: 580px;
          display: grid;
          grid-template-columns: 1fr 1fr;
          grid-template-rows: 1fr 1fr;
          grid-gap: 20px;
          .upload-container {
            grid-column-start: 1;
            grid-column-end: 3;
            width: 100%;
            height: 30px;
            // border: 1px solid rgba($color-semi-sweet-chocolate, 0.5);
            position: relative;
          }
          @include vp--1023 {
            grid-gap: 10px;
          }
          @include vp--425 {
            display: flex;
            flex-direction: column;
          }
        }
      }
      &-dwnld-btn {
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: unset;
        max-width: 280px;
        width: 100%;
        border: 2px solid $color-light-azure-craiola;
        background-color: $color-light-azure-craiola;
        &:hover,
        &:focus {
          color: $color-light-azure-craiola !important;
        }
        @include vp--1023 {
          padding: 10px;
        }
        @include vp--425 {
