      &-upload-btn-label {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row-reverse;
        justify-content: flex-start;
        &:hover {
          cursor: pointer;
          text-decoration: underline;
        }
        svg {
          margin-right: 15px;
        }
      }
      &-upload-input {
        position: absolute;
        width: 0.1px;
        height: 0.1px;
        z-index: -10;
        opacity: 0;
        user-select: none;
      }
      &-sub-title {
        @include vp--425 {
          font-size: 12px;
        }
      }
      &-articles {
        &-item {
          &-title {
            color: $color-blue-for-MF;
          }
          &-link {
            color: $color-blue-for-MF;
          }
        }
      }
    }
  }
}

