              &:first-of-type {
                width: 214px;
                @include vp--425 {
                  width: 50%;
                }
              }
            }
          }
        }
      }
      &-gifts {
        margin-top: 40px;
        &-title {
          font-weight: 600;
          font-size: 16px;
          line-height: 20px;
        }
        &-list-item {
          display: flex;
          align-items: center;
          &-pic {
            margin-right: 20px;
          }
        }
        &-rules {
          margin-top: 20px;
          display: inline-block;
          font-weight: normal;
          font-size: 14px;
          line-height: 20px;
          text-decoration-line: underline;
          color: $color-nursery-primary;
          &:hover {
            text-decoration: none;
          }
        }
      }
    }
    &__loyalty {
      margin-top: 40px;
      &-title {
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
      }
      &-table-box {
        overflow-x: auto;
      }
      &-table {
        width: 780px;
        border: 1px solid rgba($color-semi-sweet-chocolate, 0.6);
        border-collapse: collapse;
        thead {
          font-weight: normal;
          font-size: 14px;
          line-height: 20px;
          background-color: #ede4de;
        }
        th,
        td {
          padding: 10px 20px;
          border: 1px solid rgba($color-semi-sweet-chocolate, 0.6);
          text-align: left;
          font-weight: normal;
          font-size: 14px;
          line-height: 20px;
        }
      }
    }
  }
}

.ask-a-question {
  width: 100%;
  margin-top: 40px;
  padding: 40px;
  display: flex;
  justify-content: space-between;
  background-color: #cdc1b2;
  @include vp--1023 {
    flex-direction: column;
  }
  &__heading {
    flex: 0 0 321px;
    margin-right: 40px;
    color: $color-white;
    @include vp--1023 {
      flex: auto;
      width: 100%;
      max-width: 400px;
      margin-bottom: 20px;
    }
  }
  &__subtitle {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    margin-top: 10px;
  }
  &__tel-link {
    color: inherit;
    &:hover {
      text-decoration: underline;
    }
  }
  &__form {
    flex: 1 1 auto;
  }
}
