// Colors
//---------------------------------
// https://www.htmlcsscolor.com/hex/334482

// Default

$color-default-black: #000000;
$color-default-white: #ffffff;
$color-transparent: rgba(255, 255, 255, 0);

// Base
$color-white: #fff;
$color-blue: #2c39f2;
$color-blue-for-MF: #017FD6;
$color-light-azure-craiola: #1CC4E3;
$color-frosty-sky: #00BFFF;
$color-semi-sweet-chocolate: #6a3f24;
$color-alizarin: #ed1b33;
$color-fruit-salad: #4caf50;
$color-olive-drab: #70941b;
$color-stark-white: #cdc1b2;
$color-fantasy: #ede4de;
$color-nursery-primary: #DC5503;

// Gradient

$black-to-right: linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);
$green-to-yellow-right: linear-gradient(
  90deg,
  #658d1b 3.98%,
  #688f1b 13.98%,
  #70941b 24.66%,
  #7e9d1c 36.02%,
  #93aa1d 49.24%,
  #adbb1e 64.32%,
  #ced01f 79.83%,
  #f3e821 90.08%,
  #fcee21 99.98%
);

$blue-to-light-blue-right: linear-gradient(90deg, $color-blue-for-MF 3.98%, #23d7e6 99.98%);
$blue-to-light-blue-right-sirius: linear-gradient(90deg, #0385D7 3.98%, #21D1E5 99.11%);
$dark-orange-to-light-orange: linear-gradient(90deg, #DC5503 3.98%, #FCA916 99.11%);
$dark-amarant-to-light-amarant-right: linear-gradient(90deg, #96145E 3.98%, #EE4587 99.11%);
$dark-green-to-light-green: linear-gradient(90deg, #01801D 3.98%, #01D00B 99.11%);
// Shadows
// --------------------------------

$shaddow-list: 0 4px 8px rgba($color-default-black, 0.25);
$shadow-btn: 0 0 8px rgba($color-default-black, 0.12), 0 8px 8px rgba($color-default-black, 0.24);

// Typography
//---------------------------------

$font-family--montserrat: "Montserrat", "Arial", sans-serif;

// Animation
//---------------------------------

$trans--300: 0.3s ease;
$trans--600: 0.6s cubic-bezier(0.55, 0, 0.1, 1);

$delay--600: 0s 0.6s;

// Viewports
//---------------------------------

$vp--320: 320px;
$vp--425: 425px;
$vp--640: 640px;
$vp--768: 768px;
$vp--1024: 1024px;
$vp--1240: 1240px;
$vp--1280: 1280px;

// Retina
//---------------------------------

$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;
