.btn {
  @include reset-btn;

  min-width: 280px;
  padding: 10px 20px;

  font-weight: 600;
  font-size: 14px;
  line-height: 1.21;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: $color-default-white;

  background-color: $color-olive-drab;
  border: 2px solid $color-olive-drab;
  border-radius: 2px;

  box-shadow: $shadow-btn;

  transition:
    color $trans--300,
    background-color $trans--300;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:not(:disabled):hover,
    &:focus {
      color: $color-olive-drab;
      background-color: $color-transparent;
    }
  }

  &:disabled {
    opacity: 0.4;
  }

  &--dim {
    background-color: $color-stark-white;
    border-color: $color-stark-white;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:not(:disabled):hover,
      &:focus {
        color: $color-stark-white;
      }
    }
  }
}
