.custom-select {
  position: relative;

  width: 200px;
  padding: 30px 0;
  padding-left: 200px;

  select {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  &.open-up {
    .custom-select__list {
      bottom: auto;
      top: 0;
      transform: translateY(calc(-100% + 30px));
    }
  }

  &.is-open {
    .custom-select__list {
      visibility: visible;
      opacity: 1;
    }

    .custom-select__icon {
      transform: rotate(180deg);
    }

    .custom-select__label {
      opacity: 0;
    }
  }

  &.not-empty {
    .custom-select__text {
      opacity: 1;
    }

    .custom-select__label {
      opacity: 0;
    }

    .custom-select__placeholder {
      opacity: 0;
    }
  }

  &.is-invalid {
    .custom-select__error {
      opacity: 1;
    }

    .custom-select__label {
      color: $color-alizarin
    }
  }

  &.is-valid {
    .custom-select__label {
      color: $color-fruit-salad;
    }
  }

  &--placeholder {
    .custom-select__label {
      opacity: 1;
    }

    &.is-open .custom-select__label {
      opacity: 1;
    }

    &.not-empty .custom-select__label {
      opacity: 1;
    }
  }

  &.slide-up.not-empty,
  &.slide-up.is-open {
    .custom-select__label {
      transform: translateY(calc(-50% - 40px));
      opacity: 1;
    }

    .custom-select__text {
      opacity: 1;
    }
  }

  @include vp--1023 {
    margin-top: 35px;
    padding-left: 0;
  }
}

.custom-select__label {
  position: absolute;
  top: 50%;
  left: 0;

  transform: translateY(-50%);

  transition: transform $trans--300, opacity $trans--300;

  pointer-events: none;

  @include vp--1023 {
    top: -25px;
    transform: unset;
  }
}

.custom-select__placeholder {
  position: absolute;
  top: 50%;
  left: 220px;

  font-size: 14px;
  line-height: 1.43;

  transform: translateY(-50%);

  transition: transform $trans--300, opacity $trans--300;

  pointer-events: none;

  @include vp--1023 {
    left: 20px;
  }
}

.custom-select__button {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 9px 19px;

  font-size: 14px;
  line-height: 1.43;

  color: $color-semi-sweet-chocolate;

  background-color: $color-transparent;

  border: 1px solid rgba($color-semi-sweet-chocolate, 0.3);
  border-radius: 5px;
  outline: none;
  cursor: pointer;

  transition: border-color $trans--300;

  &:hover,
  &:focus {
    border-color: rgba($color-olive-drab, 0.3);
  }
}

.custom-select__list {
  position: absolute;
  bottom: 0;
  z-index: 1;
  transform: translateY(100%);

  width: 100%;
  max-height: 240px;
  margin: 0;
  padding: 0;
  overflow-y: auto;

  list-style: none;

  background-color: $color-default-white;
  border-radius: 5px;
  box-shadow: $shaddow-list;

  visibility: hidden;
  opacity: 0;

  transition: visibility $trans--300, opacity $trans--300;
}

.custom-select__item {
  padding: 10px 20px;

  font-size: 14px;
  line-height: 1.43;

  outline: none;
  cursor: pointer;

  transition: background-color $trans--300;

  &[aria-selected='true'] {
    background-color: $color-fantasy;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        background-color: $color-fantasy;
      }
    }
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      background-color: $color-fantasy;
    }
  }
  &._hidden {
    display: none;
  }
  &._show {
    display: block;
  }
}

.custom-select__error {
  position: absolute;
  bottom: 0;
  left: 0;

  color: orange;

  opacity: 0;

  transition: opacity 0.1s ease;
}

.custom-select__text {
  flex-shrink: 0;
  width: 80%;
  overflow: hidden;

  text-align: left;
  white-space: nowrap;
  text-overflow: ellipsis;

  opacity: 0;

  transition: opacity $trans--300 0.1s;

  br {
    display: none;
  }
}

.custom-select__icon {
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  margin-left: auto;

  background-color: $color-semi-sweet-chocolate;

  transition: transform $trans--300;

  clip-path: polygon(25% 40%, 75% 40%, 50% 65%);
}
