.modal {
	&--recommendations-registration {
		.modal__content {
			width: 100%;
		}
		.recommendations {
			&-registration {
				&__title {
					font-style: normal;
					font-weight: 600;
					font-size: 16px;
					line-height: 20px;
					margin: 0;
					padding: 0;
				}
			}
			&__form {
				margin: 40px 0 0 0;
				.recommendations-form__submit-btn {
					margin: 30px 0 0 0;
				}
			}
		}
	}
}
