.custom-toggle {
  position: relative;
  display: flex;
  width: 500px;
  padding: 20px 0;
  @include vp--768 {
    margin-top: 30px;
  }
  @include vp--425 {
    width: 100%;
  }

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  label {
    position: relative;

    padding-left: 30px;

    font-size: 14px;
    line-height: 1.43;

    cursor: pointer;

    span {
      margin-bottom: 0;
      margin-left: 0;
    }
  }

  input:checked + label::after {
    background-color: $color-semi-sweet-chocolate;

    opacity: 1;
  }

  input:disabled + label::before {
    opacity: 0.5;
  }

  &.is-invalid {
    label span {
      color: $color-alizarin;
    }

    .custom-toggle__error {
      opacity: 1;
    }
  }

  /*&.is-valid {
    label span {
      color: green;
    }
  }*/
}

.custom-toggle__error {
  position: absolute;
  bottom: 0;
  left: 0;

  color: $color-alizarin;

  opacity: 0;

  transition: opacity $trans--300;
}

.custom-toggle--radio {
  label {
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;

      width: 16px;
      height: 16px;

      border: 1px solid $color-semi-sweet-chocolate;
      border-radius: 50%;

      transition: border $trans--300;
    }

    &::after {
      content: "";
      position: absolute;
      top: 6px;
      left: 4px;

      width: 8px;
      height: 8px;

      background-color: $color-transparent;
      border-radius: 50%;

      transition: background-color $trans--300;
    }
  }
}

.custom-toggle--checkbox {
  label {
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;

      width: 20px;
      height: 20px;

      border: 2px solid $color-semi-sweet-chocolate;
      border-radius: 2px;

      transition: border-color $trans--300;
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 20px;
      background-image: url("../img/svg/icon-check.svg");
      // background-image: url("/local/templates/sirius/assets/img/svg/icon-check.svg"); //path for host
      background-position: center;
      background-repeat: no-repeat;
      border-radius: 2px;
      opacity: 0;
      transition: background-color $trans--300, opacity $trans--300;
    }
  }
}
